
import { defineComponent, ref } from "vue";
import { showDetailScreen } from "@/helpers/utils";

export default defineComponent({
  name: "DealItem",
  props: {
    deal: {
      type: Object,
      required: true,
    },
  },
  setup(props) {
    const item = ref(props.deal);

    return {
      item,
      showDetailScreen,
    };
  },
});
