
import { defineComponent, watch } from "vue";
import useDebouncedRef from "@/helpers/useDebouncedRef";

export default defineComponent({
  name: "Search",
  emits:['set-filters'],
  props:{
    filters:{
      type:Object
    }
  },

  setup(props, {emit}) {
    const query = useDebouncedRef("", 1000, false);

    watch(query,(title) => {  

        emit('set-filters', {
        ...props.filters,
        title,
        page: 1
      })
    });

    return {
      query
    };
  },
});
